'use client'

import { useState } from 'react'
import { executeReconTool, validateTarget } from '@/services/reconService'
import { ReconResult, ScanOptions } from '@/types/recon'

interface ScanModalProps {
  toolName: string
  isOpen: boolean
  onClose: () => void
}

export function ScanModal({ toolName, isOpen, onClose }: ScanModalProps) {
  const [target, setTarget] = useState('')
  const [intensity, setIntensity] = useState<'low' | 'medium' | 'high'>('medium')
  const [isScanning, setIsScanning] = useState(false)
  const [result, setResult] = useState<ReconResult | null>(null)
  const [error, setError] = useState('')

  if (!isOpen) return null

  const handleScan = async () => {
    setError('')
    setResult(null)
    
    try {
      // Validate target
      const isValid = await validateTarget(target)
      if (!isValid) {
        setError('Invalid target specified. Please enter a valid IP address or domain.')
        return
      }

      setIsScanning(true)
      const options: ScanOptions = {
        target,
        intensity,
        timeout: intensity === 'low' ? 30 : intensity === 'medium' ? 60 : 120
      }

      const scanResult = await executeReconTool(toolName, options)
      setResult(scanResult)
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred during the scan')
    } finally {
      setIsScanning(false)
    }
  }

  return (
    <div className="fixed inset-0 bg-black/50 backdrop-blur-sm z-50 flex items-center justify-center">
      <div className="bg-gray-900 border border-green-500/20 rounded-lg p-6 max-w-2xl w-full mx-4 shadow-xl">
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-2xl font-mono text-green-400">Launch {toolName}</h2>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-green-500 transition-colors"
          >
            âœ•
          </button>
        </div>

        <div className="space-y-4">
          <div>
            <label className="block text-sm font-mono text-gray-400 mb-1">
              Target (IP or Domain)
            </label>
            <input
              type="text"
              value={target}
              onChange={(e) => setTarget(e.target.value)}
              placeholder="example.com or 192.168.1.1"
              className="w-full px-4 py-2 bg-gray-800/50 border border-green-500/20 rounded-lg text-gray-100 placeholder-gray-500 focus:outline-none focus:border-green-500/50"
            />
          </div>

          <div>
            <label className="block text-sm font-mono text-gray-400 mb-1">
              Scan Intensity
            </label>
            <div className="flex gap-4">
              {(['low', 'medium', 'high'] as const).map((level) => (
                <button
                  key={level}
                  onClick={() => setIntensity(level)}
                  className={`px-4 py-2 rounded-lg font-mono capitalize ${
                    intensity === level
                      ? 'bg-green-500/20 text-green-400 border border-green-500/20'
                      : 'bg-gray-800/50 text-gray-400 border border-transparent hover:border-green-500/20'
                  }`}
                >
                  {level}
                </button>
              ))}
            </div>
          </div>

          {error && (
            <div className="text-red-400 bg-red-500/10 border border-red-500/20 rounded-lg p-4 font-mono">
              {error}
            </div>
          )}

          {result && (
            <div className="bg-gray-800/50 border border-green-500/20 rounded-lg p-4">
              <pre className="font-mono text-sm text-gray-300 whitespace-pre-wrap">
                {result.output}
              </pre>
            </div>
          )}

          <div className="flex justify-end gap-4 mt-6">
            <button
              onClick={onClose}
              className="px-4 py-2 text-gray-400 hover:text-green-500 transition-colors font-mono"
            >
              Cancel
            </button>
            <button
              onClick={handleScan}
              disabled={isScanning || !target}
              className={`px-6 py-2 bg-green-500/10 text-green-400 rounded-lg font-mono border border-green-500/20 
                ${isScanning || !target 
                  ? 'opacity-50 cursor-not-allowed' 
                  : 'hover:bg-green-500/20 transition-all'
                }`}
            >
              {isScanning ? 'Scanning...' : 'Launch Scan'}
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}
