"use client"

import { useState, useEffect } from "react"
import { ScanModal } from "@/components/ScanModal"
import { ToolCard } from "@/components/ToolCard"
import { getAllTools } from "@/services/reconService"
import type { ToolConfig } from "@/types/recon"

const defaultRecommendations = [
  {
    id: 1,
    title: "Advanced Network Enumeration",
    description: "Deep network scanning with service fingerprinting, vulnerability assessment, and stealth mode options",
    tools: ["Nmap", "Masscan", "Zmap", "Unicornscan"],
    features: ["Service Version Detection", "OS Fingerprinting", "IDS Evasion", "Custom Timing Templates"],
    image: "/globe.svg",
    category: "network"
  },
  {
    id: 2,
    title: "Web Application Mapping",
    description: "Discover hidden endpoints, API routes, parameters, and potential injection points",
    tools: ["Burp Suite", "OWASP ZAP", "Dirsearch", "Gobuster"],
    features: ["API Endpoint Discovery", "Parameter Fuzzing", "Authentication Bypass Detection", "Custom Wordlists"],
    image: "/window.svg",
    category: "webapp"
  },
  {
    id: 3,
    title: "Infrastructure Analysis",
    description: "Cloud infrastructure enumeration, container analysis, and service mesh mapping",
    tools: ["CloudMapper", "ScoutSuite", "AWStealth", "Prowler"],
    features: ["IAM Analysis", "S3 Bucket Enumeration", "Container Security", "Serverless Assessment"],
    image: "/file.svg",
    category: "infrastructure"
  },
  {
    id: 4,
    title: "Advanced OSINT",
    description: "Employee enumeration, document metadata analysis, and exposed credentials scanning",
    tools: ["TheHarvester", "Maltego", "Recon-ng", "SpiderFoot"],
    features: ["Email Discovery", "Social Media Mapping", "Document Analysis", "Credential Leaks"],
    image: "/globe.svg",
    category: "osint"
  },
  {
    id: 5,
    title: "SSL/TLS Analysis",
    description: "Certificate chain analysis, cipher suite evaluation, and protocol vulnerability scanning",
    tools: ["SSLyze", "TLS-Scanner", "TestSSL.sh", "CipherScan"],
    features: ["Protocol Testing", "Cipher Analysis", "Certificate Validation", "Vulnerability Detection"],
    image: "/window.svg",
    category: "crypto"
  },
  {
    id: 6,
    title: "Wireless Network Recon",
    description: "WiFi network enumeration, Bluetooth scanning, and RF spectrum analysis",
    tools: ["Aircrack-ng", "Kismet", "Wireshark", "BlueHydra"],
    features: ["WPA/WPA2 Analysis", "Bluetooth Recon", "Packet Injection", "Signal Mapping"],
    image: "/file.svg",
    category: "wireless"
  }
]

export default function Home() {
  const [searchTerm, setSearchTerm] = useState("")
  const [selectedCategory, setSelectedCategory] = useState<string | null>(null)
  const [recommendations, setRecommendations] = useState(defaultRecommendations)
  const [filteredRecommendations, setFilteredRecommendations] = useState(recommendations)
  const [selectedTool, setSelectedTool] = useState<string | null>(null)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    const loadTools = async () => {
      try {
        const tools = await getAllTools()
        const formattedTools = tools.map((tool, index) => ({
          id: index + 1,
          title: tool.name,
          description: tool.description,
          image: "/file.svg",
          category: tool.category,
          tools: [tool.name],
          features: tool.args.map(arg => arg.replace('-', ''))
        }))
        setRecommendations(formattedTools)
        setFilteredRecommendations(formattedTools)
      } catch (error) {
        console.error('Failed to load tools:', error)
      } finally {
        setIsLoading(false)
      }
    }

    loadTools()
  }, [])

  const categories = Array.from(new Set(recommendations.map(rec => rec.category)))

  const handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
    const term = e.target.value.toLowerCase()
    setSearchTerm(term)
    filterRecommendations(term, selectedCategory)
  }

  const handleCategorySelect = (category: string | null) => {
    setSelectedCategory(category)
    filterRecommendations(searchTerm, category)
  }

  const filterRecommendations = (term: string, category: string | null) => {
    const filtered = recommendations.filter(rec => {
      const matchesTerm = rec.title.toLowerCase().includes(term) || 
                         rec.description.toLowerCase().includes(term)
      const matchesCategory = !category || rec.category === category
      return matchesTerm && matchesCategory
    })
    setFilteredRecommendations(filtered)
  }

  return (
    <main className="min-h-screen bg-gray-900">
      {/* Hero Section */}
      <section className="py-24 bg-gradient-to-b from-gray-900 to-gray-800">
        <div className="container mx-auto px-4">
          <div className="relative">
            <div className="absolute inset-0 flex items-center justify-center opacity-10">
              <div className="text-[12rem] font-mono text-green-500">{`[>_]`}</div>
            </div>
            <h1 className="text-4xl md:text-6xl font-bold text-center text-gray-100 mb-6 relative">
              Automation <span className="text-green-500 font-mono">[Recon]</span>
            </h1>
          </div>
          <p className="text-xl text-center text-gray-400 mb-12 max-w-2xl mx-auto">
            Advanced penetration testing reconnaissance toolkit for comprehensive security assessments
          </p>
          <div className="max-w-2xl mx-auto relative space-y-6">
            {/* Search Input */}
            <div className="relative mb-6">
              <svg
                className="absolute left-6 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                />
              </svg>
              <input
                type="text"
                placeholder="Search recommendations..."
                value={searchTerm}
                onChange={handleSearch}
              className="w-full mb-6 pl-14 pr-6 py-4 border-2 border-green-500/20 bg-gray-800/50 rounded-2xl focus:outline-none focus:ring-2 focus:ring-green-500/20 shadow-sm transition-all duration-200 hover:shadow-green-500/5 text-gray-100 placeholder-gray-500 font-mono"
              />
            </div>
            {/* Category Filter */}
            <div className="flex flex-wrap justify-center gap-3 mb-8">
              <button
                onClick={() => handleCategorySelect(null)}
                className={`px-4 py-2 rounded-lg transition-all ${
                  !selectedCategory 
                    ? 'bg-green-500 text-gray-900' 
                    : 'bg-gray-800 text-gray-400 hover:bg-gray-700'
                }`}
              >
                All
              </button>
              {categories.map((category) => (
                <button
                  key={category}
                  onClick={() => handleCategorySelect(category)}
                  className={`px-4 py-2 rounded-lg capitalize transition-all ${
                    selectedCategory === category 
                      ? 'bg-green-500 text-gray-900' 
                      : 'bg-gray-800 text-gray-400 hover:bg-gray-700'
                  }`}
                >
                  {category}
                </button>
              ))}
            </div>
          </div>
        </div>
      </section>

      {/* Recommendations Section */}
      <section className="py-16">
        <div className="container mx-auto px-4">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {filteredRecommendations.map((rec) => (
              <ToolCard
                key={rec.id}
                {...rec}
                onLaunch={setSelectedTool}
              />
            ))}
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="py-24 bg-gradient-to-b from-gray-800 to-gray-900">
        <div className="container mx-auto px-4 text-center">
          <h2 className="text-4xl font-bold mb-6">Start Reconnaissance</h2>
          <p className="text-xl text-gray-400 mb-10 max-w-2xl mx-auto">
            Launch sophisticated reconnaissance operations with our advanced pentesting toolkit
          </p>
          <button className="group bg-green-500/10 text-green-400 font-mono font-semibold px-8 py-4 rounded-xl hover:bg-green-500/20 transition-all duration-200 transform hover:scale-[1.02] shadow-lg border border-green-500/20">
            <span className="group-hover:hidden">[</span>
            Get Started
            <span className="group-hover:hidden">]</span>
          </button>
        </div>
      </section>

      {/* Scan Modal */}
      {selectedTool && (
        <ScanModal
          toolName={selectedTool}
          isOpen={!!selectedTool}
          onClose={() => setSelectedTool(null)}
        />
      )}
    </main>
  )
}
